import { Todo, TodoStatus } from "../../typings/app";

const statusToIconMap: Record<TodoStatus, string> = {
  [TodoStatus.COMPLETED]: 'fa-check-circle success-color',
  [TodoStatus.ENTERED]: 'fa-play accent-color',
  [TodoStatus.INPROCESS]: 'fa-spinner active-color',
}

export const listElementRenderer = (item: Todo) => `
  <li class="ph-2" data-todo-id="${item.id}"><div>
      <i class="mr-5 fas ${statusToIconMap[item.status]}" title="${(item?.status ?? '').toLowerCase()}"></i>
      <span class="bold break-line">${item.title}</span> -
      <span class="italic break-line">${item.description}</span>
      <button name="remove-icon" class="ml-5 icon-button" title="delete item">
        <i class="fas fa-trash-alt danger-color"></i>
      </button>
      <button name="edit-icon" class="icon-button" title="edit item">
        <i class="fas fa-pen-alt"></i>
      </button>
    </div>
  </li>
`

export const listElementEditRenderer = (item: Todo) => `
    <form name="edit-todo" data-todo-edit-id="${item.id}" class="d-flex justify-content">
      <input value="${item.title}" class="input" name="title" required type="text" maxlength="100" placeholder="name your activtiy">
      <textarea name="description" class="input textarea-horizontal" required type="text" maxlength="500" placeholder="describe it...">${item.description}</textarea>
      <input value="${item.expiryDate}" class="input" name="expiryDate" required type="date">
      <select class="input" name="status">
        <option value="ENTERED" ${item.status === TodoStatus.ENTERED ? 'selected': ''}>entered</option>
        <option value="INPROCESS" ${item.status === TodoStatus.INPROCESS ? 'selected': ''}>in process</option>
        <option value="COMPLETED" ${item.status === TodoStatus.COMPLETED ? 'selected': ''}>completed</option>
      </select>
      <div class="d-flex dir-col justify-content">
        <button name="cancel-modification" class="ml-5 rounded danger-color cursor-pointer" type="submit">Cancel</button>
        <button class="ml-5 rounded success-color cursor-pointer" type="submit">Save</button>
      </div>
    </form>
`

export const listElementNode = (item: Todo, renderer: (item: Todo) => string) => {
  const template = document.createElement('template');
  template.innerHTML = renderer(item);
  return template.content;
}