import { Todo } from '../typings/app';
import { getAncestorWithType } from './helpers/index';
import { listElementRenderer, listElementEditRenderer, listElementNode } from './List/index';
import './styles/style.css';
import '@fortawesome/fontawesome-free/css/all.css';
import { createEmptyTodo, mockedTodos } from './model/todo/index';
// template polyfil

document.addEventListener("DOMContentLoaded", function () {
    new TodoApp(mockedTodos)
});

class TodoApp {

    todoListElement: Element;

    containerElement: Element;

    newTodo: Todo;

    todos: Todo[];

    todoUpdates: Record<string, Partial<Todo>> = {}

    constructor(todos: Todo[]) {
        // DOM nodes
        this.containerElement = window.document.querySelector('#todo-container');
        this.todoListElement = this.containerElement.querySelector('#todo-list');
        
        // data model
        this.newTodo = createEmptyTodo();
        this.todos = todos;

        this.handleEventListeners();
        this.renderList();
    }

    handleEventListeners = () => {
        this.containerElement.addEventListener('change', (event: Event) => {
            const { name, value, parentElement } = (event.target as HTMLInputElement);
            const updatedTodoId = parentElement?.getAttribute('data-todo-edit-id');

            if (updatedTodoId) {
                return this.handleUpdates(updatedTodoId, { [name]: value })
            }

            (this.newTodo as any)[name] = value
        })

        this.containerElement.addEventListener('submit', (event: Event) => {
            event.preventDefault();
            const { name: formName } = (event.target as HTMLFormElement);

            switch (formName) {
                case 'create-todo':
                    this.addItem();
                    this.newTodo = createEmptyTodo();
                    break;
                case 'edit-todo':
                    const updatedTodoId = (event.target as HTMLFormElement)?.getAttribute('data-todo-edit-id');
                    const index = this.todos.findIndex(item => item.id === updatedTodoId);

                    if (this.todoUpdates[updatedTodoId]) {
                        this.todos[index] = { ...this.todos[index], ...this.todoUpdates[updatedTodoId] };
                        this.todoUpdates[updatedTodoId] = null;
                    }

                    this.replaceModificationState(updatedTodoId);
                    break;
                default:
                    break;
            }

            (event.target as HTMLFormElement).reset();
        })

        this.todoListElement.addEventListener('click', (event: Event) => {
            const { name } = <HTMLInputElement>((event.target as HTMLInputElement).nodeName === 'BUTTON' ? event.target : (event.target as HTMLInputElement).parentElement)
            
            const todoId = getAncestorWithType(event.target as Element, 'LI')?.getAttribute('data-todo-id');

            if (name && todoId) {
                switch (name) {
                    case 'remove-icon':
                        this.getListElement(todoId).remove();
                        this.todos = this.todos.filter(item => item.id !== todoId);
                        break;
                    case 'edit-icon':
                        this.editItem(todoId)
                        break;
                    case 'cancel-modification':
                        this.replaceModificationState(todoId);
                        break;

                    default:
                        break;
                }
            }
        })
    }

    renderList = () => {
        this.todoListElement.innerHTML = this.todos.map(listElementRenderer).join('')
    }

    getListElement = (todoId: string) => this.todoListElement.querySelector(`[data-todo-id="${todoId}"]`)

    addItem = () => {
        this.todoListElement.prepend(listElementNode(this.newTodo, listElementRenderer));
        this.todos.unshift(this.newTodo);
    }

    handleUpdates = (elementId: string, update: Record<string, string>) => {
        if (!this.todoUpdates[elementId]) {
            this.todoUpdates[elementId] = {
                ...update
            }
        } else {
            this.todoUpdates[elementId] = {
                ...this.todoUpdates[elementId],
                ...update,
            }
        }
    }

    editItem = (todoId: string) => {
        const editedItem = this.todos.find(todo => todo.id === todoId);
        const listElement = this.getListElement(todoId);

        listElement.replaceChild(listElementNode(editedItem, listElementEditRenderer), listElement.childNodes[0]);
    }

    replaceModificationState = (todoId: string) => {
        const todo = this.todos.find(todo => todo.id === todoId);
        const listElement = this.getListElement(todoId);

        this.todoListElement.replaceChild(listElementNode(todo, listElementRenderer), listElement);
    }
}

