export const getAncestorWithType = (element: Element, nodeName: string): Element | undefined => {
    if (element.nodeName === nodeName) {
        return element;
    }

    if (element.parentElement) {
        if (element.parentElement.nodeName === nodeName) {
            return element.parentElement;
        }

        return getAncestorWithType(element.parentElement, nodeName)
    }
}
